export function priceLine(target, params) {
    const subject = target.subject().createPriceLine(params);
    return {
        subject() {
            return subject;
        },
        update(nextParams) {
            if (nextParams) {
                subject.applyOptions(nextParams);
            }
        },
        destroy() {
            if (target.alive()) {
                target.subject().removePriceLine(subject);
            }
        }
    };
}
